<template>
  <div class="mt-2">
    <v-row class="align-center">
      <h2 class="mr-4">Results</h2>
      <v-btn @click="expandAll" x-small> Expand All</v-btn>
      <v-btn @click="collapseAll" x-small class="ml-4"> Close All</v-btn>

      <v-spacer />

      <v-btn
        class="ma-2 mr-4"
        small
        outlined
        download
        :href="csv"
        @click="downloadCSVData"
        >Download CSV
      </v-btn>
      <AxesManipulation
        :axes="axes"
        @yAxisChanged="yAxisChanged($event)"
        @xAxisChanged="xAxisChanged($event)"
      />
    </v-row>

    <v-row>
      <v-col cols="3">
        <Filters
          :panels="panels"
          :filters="filters"
          @panelChanged="updatePanels($event)"
          @activeRangeChanged="activeRangeChanged"
        />
      </v-col>
      <v-col cols="9">
        <!--  Any Changes to active range should be set in Dynamic instance only-->
        <v-card elevation="1" class="mb-4 pt-4 pb-2">
          <!--        staticScatterplotData {{ staticScatterplotData.length }}-->
          <!--          dynamicScatterplotData {{ dynamicScatterplotData.length }}-->
          <v-row>
            <v-col cols="6">
              <Scatterplot
                chartId="engora-products-scatterplot-dynamic"
                :data="dynamicScatterplotData"
                :xAxisLabel="xAxisLabel"
                :yAxisLabel="yAxisLabel"
                :overPoint="overPoint"
                :sessionID="sessionID"
                @pointOver="dynamicScatterplotPointOver"
                @pointOut="dynamicScatterplotPointOut"
              />
            </v-col>
            <v-col cols="6">
              <Scatterplot
                chartId="engora-products-scatterplot-static"
                :isStatic="true"
                :data="staticScatterplotData"
                :dynamicData="dynamicScatterplotData"
                :xAxisLabel="xAxisLabel"
                :yAxisLabel="yAxisLabel"
                :overPoint="overPoint"
                :sessionID="sessionID"
                @pointOver="staticScatterplotPointOver"
                @pointOut="staticScatterplotPointOut"
              />
            </v-col>
          </v-row>
        </v-card>

        <!--        TODO - for filtering table contents by currentFiltersWithActiveRanges-->
        <!--        Debug {{ currentFiltersWithActiveRanges }}-->

        <!--        test {{ results[0].values }}-->

        <ResultsTable
          :headers="headers"
          :tableItems="tableItems"
          :exampleRow="exampleRow"
          :parser="parser"
          :alphabet="alphabet"
          :originalColumnsIDs="originalColumnsIDs"
          :sessionID="sessionID"
          @addColumn="addColumn"
          @removeNewColumn="removeNewColumn"
          @updateNewColumnName="updateNewColumnName"
        />
      </v-col>
    </v-row>
  </div>
</template>

<script>
import Scatterplot from "@/components/Scatterplot";
import Filters from "@/components/Filters";
import AxesManipulation from "@/components/AxesManipulation";
import ResultsTable from "@/components/ResultsTable";
import { logEvent } from "@/services/logger";

export default {
  props: ["results", "parser", "alphabet", "originalColumnsIDs", "sessionID"],
  components: {
    Scatterplot,
    Filters,
    AxesManipulation,
    ResultsTable,
  },
  data: () => ({
    axes: [],
    headers: [],
    tableItems: [],
    staticScatterplotData: [],
    dynamicScatterplotData: [],
    outsideDynamicScatterplotData: [],
    xAxisLabel: "",
    yAxisLabel: "",
    csv: null,
    panels: [],
    capacityKey: "capacity (As)",
    filters: [],
    currentFiltersWithActiveRanges: {},
    exampleRow: {},
    overPoint: null,
  }),
  methods: {
    initAxes() {
      this.axes = [];
      for (const [key] of Object.entries(this.results[0].values)) {
        this.axes.push(key);
      }
    },
    initFilterPanels() {
      // currently all expanded
      this.panels = [];
      this.axes.forEach((axis, i) => {
        this.panels.push(i);
      });
    },
    updatePanels(i) {
      if (this.panels.includes(i)) {
        this.panels.splice(this.panels.indexOf(i), 1);
      } else {
        this.panels.push(i);
      }
    },
    initActiveAxes() {
      if (this.xAxisLabel.length === 0 && this.yAxisLabel.length === 0) {
        this.xAxisLabel = this.axes[0];

        // handle only price axes returned
        if (this.axes[1]) {
          this.yAxisLabel = this.axes[1];
        } else {
          this.yAxisLabel = this.axes[0];
        }
      }
    },
    setStaticScatterplotData() {
      this.staticScatterplotData = [];
      this.results.forEach((e, index) => {
        // first index is X, second is Y
        let point = {
          id: index + 1,
          x: e.values[this.xAxisLabel][0],
          y: e.values[this.yAxisLabel][0],
          link: e.product[1],
          name: e.product[2],
          isActive: true,
        };
        this.staticScatterplotData.push(point);
      });
    },
    initDynamicScatterplotData() {
      this.dynamicScatterplotData.push(...this.staticScatterplotData);
    },
    setDynamicScatterplotData() {
      let notActivePointColor = "#DBE2EA";
      let activePointColor = "#7cb5ec";

      this.staticScatterplotData = [];
      this.dynamicScatterplotData = [];
      this.outsideDynamicScatterplotData = [];
      // check if is in active range of all filters.
      this.results.forEach((e, index) => {
        let isInActiveRangesMap = [];
        // iterate over all current filters ranges
        for (const [key, value] of Object.entries(e.values)) {
          let min = this.currentFiltersWithActiveRanges[key][0];
          let max = this.currentFiltersWithActiveRanges[key][1];
          if (min <= value[0] && value[0] <= max) {
            isInActiveRangesMap.push(1);
          } else {
            isInActiveRangesMap.push(0);
          }
        }

        let point = {
          id: index + 1,
          x: e.values[this.xAxisLabel][0],
          y: e.values[this.yAxisLabel][0],
          name: e.product[2],
          link: e.product[1],
          isActive: true,
          color: activePointColor,
          values: e.values,
        };

        // insert only if point in N active ranges // outside if at least out of 1 range
        if (!isInActiveRangesMap.includes(0)) {
          this.dynamicScatterplotData.push(point);
          this.staticScatterplotData.push(point);
        } else {
          this.outsideDynamicScatterplotData.push(point);

          point.isActive = false;
          point.color = notActivePointColor;
          this.staticScatterplotData.push(point);
        }
      });
    },
    downloadCSVData() {
      let filename = "results.csv";
      let csvContent = "data:text/csv;charset=utf-8,";

      csvContent += '"' + "URL" + '","' + "Name" + '",';
      for (let axeLabel of this.axes) {
        csvContent += ' "' + axeLabel + '" ,';
      }
      csvContent += "\r\n";

      for (let d of this.results) {
        csvContent +=
          '"' +
          "https://" +
          d.product[1] +
          '","' +
          d.product[2].replace('"', '""') +
          '",';
        for (let axeLabel of this.axes) {
          csvContent += "" + d.values[axeLabel][0] + ",";
        }
        csvContent += "\r\n";
      }
      this.csv = encodeURI(csvContent);

      let link = document.createElement("a");
      link.setAttribute("href", this.csv);
      link.setAttribute("download", filename);
      link.click();
    },
    yAxisChanged(newVal) {
      this.yAxisLabel = newVal;
      this.setStaticScatterplotData();
      this.setDynamicScatterplotData();
    },
    xAxisChanged(newVal) {
      this.xAxisLabel = newVal;
      this.setStaticScatterplotData();
      this.setDynamicScatterplotData();
    },
    expandAll() {
      this.panels = [];
      this.axes.forEach((axis, i) => {
        this.panels.push(i);
      });
    },
    collapseAll() {
      this.panels = [];
    },
    initTableHeaders() {
      this.headers = [{ text: "Name", value: "name" }];
      Object.keys(this.results[0].values).forEach((label) => {
        // let uniqueColumnId = this.alphabet[index].toUpperCase();
        let headerToPush = {
          text: `${label}`,
          value: label,
          isHover: false,
        };

        this.headers.push(headerToPush);
      });
    },
    initTableItems() {
      this.tableItems = [];
      this.results.forEach((entry) => {
        let tableItem = {
          name: entry.product[2],
          link: entry.product[1],
        };
        this.assignDynamicAmountOfValuesToTableItem(entry, tableItem);
        this.tableItems.push(tableItem);
      });
    },
    updateTableItems() {
      this.tableItems = [];
      this.dynamicScatterplotData.forEach((entry) => {
        let tableItem = {
          name: entry.name,
          link: entry.link,
        };
        this.assignDynamicAmountOfValuesToTableItem(entry, tableItem);
        this.tableItems.push(tableItem);
      });
    },
    assignDynamicAmountOfValuesToTableItem(entry, tableItem) {
      for (const [key, value] of Object.entries(entry.values)) {
        if (key !== "price") {
          // general formatting for values that are not Price / Name / Link
          tableItem[key] = value[0].toPrecision(3);
        } else {
          tableItem[key] = value[0];
        }
      }
    },
    initExampleRow() {
      let indexCounter = 0;
      this.exampleRow = {};
      for (const [key, value] of Object.entries(this.results[0].values)) {
        this.exampleRow[this.alphabet[indexCounter].toUpperCase()] = {
          key: key,
          value: value[0],
        };
        indexCounter++;
      }
    },
    initFiltersByCurrentAxes() {
      // console.log("initFiltersByCurrentAxes ", this.axes);
      this.filters = [];
      this.axes.forEach((axis) => {
        let filterToPush = {
          key: axis,
          values: [],
        };
        let isOriginal = this.originalColumnsIDs.includes(axis);
        if (isOriginal) {
          filterToPush["isOriginal"] = true;
        } else {
          filterToPush["isOriginal"] = false;
        }
        this.filters.push(filterToPush);
      });
      // console.log("filters ", this.filters);
    },
    initFiltersAdditionalData() {
      this.results.forEach((entry) => {
        for (const [filter, value] of Object.entries(entry.values)) {
          this.filters.forEach((range) => {
            if (range.key === filter) {
              range.values.push(value[0]);
            }
          });
        }
      });

      this.filters.forEach((filter) => {
        filter.min = Math.min(...filter.values);
        filter.max = Math.max(...filter.values);
        filter.range = [filter.min, filter.max];

        this.currentFiltersWithActiveRanges[filter.key] = filter.range;
      });
    },
    initAbsMinMaxAndDynamicStepSize() {
      // Given bases 0.1 and 0.5 we find a step size <base value>*Math.pow(10, n)
      // Which gives us approximately n_steps steps
      // for round-looking numbers with a fairly smooth slider action and an inclusive range
      this.filters.forEach((filter) => {
        let n_steps = 1000;
        let exact_step = (filter.max - filter.min) / n_steps;
        let bases = [0.5, 0.1];
        let sample_step = bases[0];
        let steps = (filter.max - filter.min) / sample_step;
        if (steps < n_steps) {
          while (sample_step > exact_step) {
            sample_step = bases[bases.length - 1];
            bases.push(bases[bases.length - 2] / 10.0);
          }
        } else {
          while (sample_step < exact_step) {
            sample_step = bases[bases.length - 1];
            bases.push(bases[bases.length - 2] * 10.0);
          }
        }
        let step = sample_step;
        let lower_limit = Math.floor(filter.min / step) * step;
        lower_limit = parseFloat(lower_limit.toFixed(10));
        if (lower_limit > filter.min) {
          lower_limit = lower_limit - step;
        }
        let upper_limit = Math.ceil(filter.max / step) * step;
        upper_limit = parseFloat(upper_limit.toFixed(10));
        if (upper_limit < filter.max) {
          upper_limit = upper_limit + step;
        }

        filter.stepSize = step;
        filter.absMin = lower_limit;
        filter.absMax = upper_limit;
        filter.currMin = lower_limit;
        filter.currMax = upper_limit;
        filter.range = [lower_limit, upper_limit];
      });
    },
    updateFilters(updatedFilter) {
      this.currentFiltersWithActiveRanges[updatedFilter.key] =
        updatedFilter.activeRange;
    },
    activeRangeChanged(filter) {
      console.log("filter changed ", filter);
      this.logFilterUsedEvent(filter);
      this.updateFilters(filter);
      this.setDynamicScatterplotData(filter);
      this.updateTableItems();
    },
    updateResultsData(payload) {
      this.$emit("updateResultsData", payload);
    },
    updateTableHeaders(payload) {
      let isNewHeader = true;
      this.headers.forEach((header) => {
        if (header.value === payload.header.value) {
          isNewHeader = false;
        }
      });

      if (isNewHeader) {
        let headerToPush = {
          text: `${payload.header.text}`,
          value: payload.header.value,
          isHover: false,
        };

        this.headers.push(headerToPush);
      }
    },
    updateSpecificTableHeader(payload) {
      this.headers.forEach((header) => {
        if (header.value === payload.value) {
          header.text = payload.text;
        }
      });
    },
    addColumn(payload) {
      this.logNewAddedColumn(payload);
      // Currently mutates the results array
      this.updateResultsData(payload);
      // local updates
      this.initExampleRow();
      this.updateTableHeaders(payload);
      this.initTableItems();
      this.initFilters();
    },
    updateNewColumnName(payload) {
      this.updateSpecificTableHeader(payload);
      this.$emit("updateColumnNameQueryParam", payload);
    },
    removeNewColumn(payload) {
      this.$emit("removeNewColumn", payload);
      this.initTableHeaders();
      this.initTableItems();
      this.initFilters();
    },
    initFilters() {
      this.initAxes();
      this.initFilterPanels();
      this.initFiltersByCurrentAxes();
      this.initFiltersAdditionalData();
      this.initAbsMinMaxAndDynamicStepSize();
    },
    dynamicScatterplotPointOver(point) {
      // check by x y not id(different id in each plot)
      this.overPoint = {
        x: point.x,
        y: point.y,
      };
    },
    dynamicScatterplotPointOut() {
      this.overPoint = null;
    },
    staticScatterplotPointOver(point) {
      // check by x y not id(different id in each plot)
      this.overPoint = {
        x: point.x,
        y: point.y,
      };
    },
    staticScatterplotPointOut() {
      this.overPoint = null;
    },
    // * * * Log Triggers * * *
    logFilterUsedEvent(filter) {
      let body = {
        event: "filter",
        filterName: filter.key,
        activeRange: filter.activeRange,
        previousRange: filter.previousRange,
        sessionID: this.sessionID,
        timestamp: Date.now(),
      };
      logEvent(body);
    },
    logNewAddedColumn(data) {
      let body = {
        event: "add-column",
        sessionID: this.sessionID,
        timestamp: Date.now(),
        formula: data.formula.value,
        formulaName: data.formula.key,
      };
      logEvent(body);
    },
  },
  beforeMount() {
    this.expandAll();

    this.initTableHeaders();
    this.initTableItems();
    this.initExampleRow();

    this.initFilters();

    this.initActiveAxes();
    this.setStaticScatterplotData();
    this.initDynamicScatterplotData();
  },
};
</script>
