<template>
  <div>
    <!--    debug overPoint {{ overPoint }}-->
    <!--    debug {{ data.length }}-->
    <!--    debug {{ chartInstance }}-->
    <!--    {{ data }}-->
    <!--    debug {{ xAxisLabel }}-->
    <div :id="chartId"></div>
  </div>
</template>

<script>
import Highcharts from "highcharts";
import * as highchartsMore from "highcharts/highcharts-more";
import { logEvent } from "@/services/logger";
highchartsMore(Highcharts);
export default {
  name: "Scatterplot",
  props: [
    "chartId",
    "isStatic",
    "data",
    "dynamicData",
    "xAxisLabel",
    "yAxisLabel",
    "overPoint",
    "sessionID",
  ],
  data: () => ({
    chartInstance: null,
  }),
  methods: {
    renderPlot() {
      const defaultHighchartsLabelColor = "#666666";
      // const defaultColor = "#7cb5ec";
      const vuetifyPrimaryBlue = "#2B81D6";
      // const defaultGridLinesColor = "#E6E6E6";
      // const darkerGray = "#E6E6E6"; // original grid line color
      const darkerGray = "#d2d2d2"; // two shades darker
      // const threeShadesDarker = "#c9c9c9";
      // const fourShadesDarker = "#c9c9c9";
      // const fourShadesDarker = "#bfbfbf";
      // const fiveShadesDarker = "#b5b5b5";
      // const sixShadesDarker = "#ababab";
      // const sevenShadesDarker = "#a1a1a1";
      // const eightShadesDarker = "#989898";
      // const nineShadesDarker = "#8e8e8e";
      // const tenShadesDarker = "#848484";
      let that = this;
      this.chartInstance = Highcharts.chart(this.chartId, {
        chart: {
          type: "scatter",
          // zoomType: this.isStatic ? "undefined" : "xy",
          height: 500,
          events: {
            click: function (e) {
              that.shouldTriggerTooltipLinkClick(e);
            },
          },
        },

        tooltip: {
          style: {
            pointerEvents: "auto",
          },
          stickOnContact: true,
          useHTML: true,
          formatter: function () {
            let validHref = `https://${this.point.link}`;
            return `<b style="font-size:14px;"><a class="tooltip-link" href="${validHref}" target="_blank">${this.point.name}</a></b><br><div>${this.x} ${that.xAxisLabel}</div><br><div>${this.y} ${that.yAxisLabel}</div>`;
          },
        },

        title: {
          text: null,
        },

        credits: {
          enabled: false,
        },

        subtitle: {
          text: null,
        },

        xAxis: {
          labels: {
            enabled: true,
          },
          title: {
            enabled: this.isStatic,
            text: this.xAxisLabel,
            style: {
              color: defaultHighchartsLabelColor,
            },
          },
          // labels: {
          //   style: {
          //     color: darkerGray,
          //   },
          // },
          gridLineWidth: 2,
          gridLineDashStyle: "ShortDot", // "Dot" might fit as well
          gridLineColor: darkerGray,
          tickLength: 0,
          lineWidth: 0,
          lineColor: "transparent",
          minorGridLineWidth: 0,
          minorTickLength: 0,
        },

        yAxis: {
          labels: {
            enabled: true,
          },
          title: {
            text: this.yAxisLabel,
            // style: {
            //   color: this.isStatic ? "transparent" : darkerGray,
            // },
          },
          // labels: {
          //   style: {
          //     color: darkerGray,
          //   },
          // },
          gridLineWidth: 2, // controls all horizontal grid lines
          gridLineDashStyle: "Dot",
          gridLineColor: darkerGray,
          tickLength: 0,
          lineWidth: 0,
          lineColor: "transparent",
          minorGridLineWidth: 0,
          minorTickLength: 0,
        },

        legend: {
          enabled: false,
          layout: "vertical",
          align: "left",
          verticalAlign: "top",
          x: 100,
          y: 70,
          floating: true,
          backgroundColor: Highcharts.defaultOptions.chart.backgroundColor,
          borderWidth: 1,
        },

        plotOptions: {
          scatter: {
            marker: {
              radius: 5,
              states: {
                hover: {
                  enabled: true,
                  lineColor: "rgb(100,100,100)",
                },
              },
            },
            states: {
              hover: {
                marker: {
                  enabled: false,
                },
              },
            },
            // tooltip: {
            //   headerFormat: "<b>{point.name}</b><br>",
            //   pointFormat: `<div>{point.x} ${this.xAxisLabel}</div><br><div>{point.y} ${this.yAxisLabel}</div>`,
            // },
          },
        },

        series: [
          {
            name: "Product",
            color: vuetifyPrimaryBlue,
            data: this.data,
            point: {
              events: {
                mouseOver: function () {
                  // if (!that.isStatic) {
                  that.$emit("pointOver", this);
                  // }
                },
                mouseOut: function () {
                  // if (!that.isStatic) {
                  that.$emit("pointOut", this);
                  // }
                },
              },
            },
          },
          {
            type: "polygon",
            data: [],
            color: Highcharts.color(Highcharts.getOptions().colors[0])
              .setOpacity(0.5)
              .get(),
            enableMouseTracking: false,
          },
        ],
      });

      this.shouldRenderActiveDynamicMiniMap();
    },
    shouldRenderActiveDynamicMiniMap() {
      if (this.isStatic) {
        // needed redraw + oneToOne to update series
        const vuetifyPrimaryBlue = "#2B81D6";
        this.chartInstance.update(
          {
            series: [
              {
                name: "Product",
                color: vuetifyPrimaryBlue,
              },
              {
                type: "polygon",
                lineWidth: 2,
                data: this.getPolygonCoordsData(),
                color: Highcharts.color(Highcharts.getOptions().colors[0])
                  .setOpacity(0.5)
                  .get(),
                enableMouseTracking: false,
                animation: false,
              },
            ],
          },
          true,
          true
        );
      }
    },
    getPolygonCoordsData() {
      // needs the dynamic data to determine polygon size
      let seriesData = [...this.dynamicData];

      let sortedXMinMaxDynamicData = seriesData.sort(function (a, b) {
        return a.x - b.x;
      });
      let x1 = sortedXMinMaxDynamicData[0].x;
      let x2 = sortedXMinMaxDynamicData[sortedXMinMaxDynamicData.length - 1].x;
      let sortedYMinMaxDynamicData = seriesData.sort(function (a, b) {
        return a.y - b.y;
      });
      let y1 = sortedYMinMaxDynamicData[0].y;
      let y2 = sortedYMinMaxDynamicData[sortedYMinMaxDynamicData.length - 1].y;

      const xBuffer = (x2 - x1) / seriesData.length;
      const yBuffer = (y2 - y1) / seriesData.length;

      return [
        [x1 - xBuffer, y1 - yBuffer],
        [x1 - xBuffer, y2 + yBuffer],
        [x2 + xBuffer, y2 + yBuffer],
        [x2 + xBuffer, y1 - yBuffer],
      ];
    },
    updateDynamicScatterplotData() {
      this.chartInstance.update(
        {
          series: [
            {
              data: this.data,
            },
          ],
        },
        true,
        true
      );
    },
    updateStaticScatterplotData() {
      this.chartInstance.update(
        {
          series: [
            {
              data: this.data,
            },
          ],
        },
        true,
        true
      );
    },
    updateStaticScatterplotCurrentHoveredPoint() {
      const HOVER_STATE = "hover";
      let pointToActivate = null;
      if (this.overPoint) {
        this.chartInstance.series[0].points.forEach((p) => {
          this.shouldResetHoveredPoint(p, HOVER_STATE);
          if (p.x === this.overPoint.x && p.y === this.overPoint.y) {
            pointToActivate = p;
          }
        });

        if (pointToActivate) {
          pointToActivate.setState(HOVER_STATE);
        }
      } else {
        this.resetStaticScatterplotHoveredPoints(HOVER_STATE);
      }
    },
    shouldResetHoveredPoint(p, HOVER_STATE) {
      if ((p.state = HOVER_STATE)) {
        p.setState();
      }
    },
    resetStaticScatterplotHoveredPoints(HOVER_STATE) {
      this.chartInstance.series[0].points.forEach((p) => {
        this.shouldResetHoveredPoint(p, HOVER_STATE);
      });
    },
    logLinkClickEvent(link) {
      let body = {
        event: "link-click",
        link: link,
        originateFrom: this.$options.name,
        sessionID: this.sessionID,
        timestamp: Date.now(),
      };
      logEvent(body);
    },
    shouldTriggerTooltipLinkClick(e) {
      // Highcharts tooltips are dynamically appended to the DOM after page load, need to trigger log after render on specific element
      let tooltipLinkElement = e.target.classList.contains("tooltip-link");
      if (tooltipLinkElement) {
        this.logLinkClickEvent(e.target.href);
      }
    },
  },
  // TODO - on change of these value use the .update method on chart instance to SMART update what is relevant
  watch: {
    xAxisLabel() {
      this.chartInstance.update({
        series: [
          {
            data: this.data,
          },
        ],
        xAxis: {
          title: {
            text: this.xAxisLabel,
          },
        },
      });
    },
    yAxisLabel() {
      this.chartInstance.update({
        series: [
          {
            data: this.data,
          },
        ],
        yAxis: {
          title: {
            text: this.yAxisLabel,
          },
        },
      });
    },
    data() {
      // render only changes needed - redraw + oneByOne set to true
      if (!this.isStatic) {
        this.updateDynamicScatterplotData();
      }

      if (this.isStatic) {
        this.updateStaticScatterplotData();
        this.shouldRenderActiveDynamicMiniMap();
      }
    },
    overPoint() {
      this.updateStaticScatterplotCurrentHoveredPoint();
    },
  },
  mounted() {
    window.logLinkClickEvent = this.logLinkClickEvent;
    this.renderPlot();
  },
};
</script>

