<template>
  <v-dialog value="true" width="500">
    <v-card>
      <v-card-title class="headline"> Edit Column Name </v-card-title>

      <v-card-text>
        <v-text-field
          v-model="header.uniqueId"
          label="Unique Column ID"
          disabled
        ></v-text-field>
        <v-text-field v-model="header.text" label="Column Name"></v-text-field>
        <v-text-field
          v-model="header.value"
          label="Formula"
          disabled
        ></v-text-field>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" text @click="closeEditColumnNameDialog">
          Cancel
        </v-btn>
        <v-btn color="primary" text @click="updateColumnName"> Update </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ["header"],
  methods: {
    closeEditColumnNameDialog() {
      this.$emit("close");
    },
    updateColumnName() {
      this.$emit("edited", this.header);
    },
  },
};
</script>
