<template>
  <div>
    <div class="d-flex pt-4 pb-4">
      <v-spacer />
      <v-btn @click="toggleAddColumn">
        <v-icon v-if="!isAddingColumn">mdi-plus</v-icon>
        <v-icon v-if="isAddingColumn">mdi-close</v-icon>
      </v-btn>
    </div>

    <div class="d-flex">
      <v-data-table
        :headers="headers"
        :items="tableItems"
        :items-per-page="5"
        class="elevation-1"
        :hide-default-footer="true"
        disable-pagination
      >
        <template v-if="headers" v-slot:header>
          <thead>
            <tr>
              <th></th>
              <th
                v-for="(header, index) in noNameHeaderHeaders"
                :key="index"
                @mouseover="headerMouseover(header)"
                @mouseleave="headerMouseleave(header)"
              >
                <div class="d-flex align-center">
                  <span class="text-h6 font-weight-bold">{{
                    getColumnUniqueId(index)
                  }}</span>
                  <v-btn
                    v-if="shouldShowRemoveColumn(index)"
                    icon
                    @click="removeNewColumn(header)"
                  >
                    <v-icon class="text-h5">mdi-delete</v-icon>
                  </v-btn>
                  <v-btn
                    v-if="shouldShowRemoveColumn(index) && header.isHover"
                    icon
                    @click="editNewColumnName(header, index)"
                  >
                    <v-icon class="text-h5">mdi-pencil</v-icon>
                  </v-btn>
                  <v-btn class="edit-btn-placeholder" v-else icon></v-btn>
                </div>
              </th>
            </tr>
          </thead>
        </template>

        <template #item.name="{ item }">
          <a
            @click="logLinkClickEvent(item.link)"
            :href="absHref(item.link)"
            target="_blank"
            style="text-decoration: none; color: #2b81d6"
          >
            {{ item.name }}
          </a>
        </template>

        <template #item.price="{ item }">
          {{ formatPrice(item.price) }}
        </template>

        <!--        <template #item.default="{ item }">{{ item + "-&#45;&#45;" }}</template>-->
      </v-data-table>

      <AddColumn
        v-if="isAddingColumn && parser"
        :exampleRow="exampleRow"
        :parser="parser"
        @addColumn="addColumn($event)"
      ></AddColumn>
    </div>

    <EditColumnNameDialog
      :header="editedHeader"
      v-if="isEditingNewColumnName"
      @close="closeEditColumnNameDialog"
      @edited="updateColumnName"
    />
  </div>
</template>

<script>
import AddColumn from "@/components/AddColumn";
import EditColumnNameDialog from "@/components/EditColumnNameDialog";
import { logEvent } from "@/services/logger";
export default {
  name: "ResultsTable",
  components: {
    AddColumn,
    EditColumnNameDialog,
  },
  props: [
    "headers",
    "tableItems",
    "parser",
    "exampleRow",
    "alphabet",
    "originalColumnsIDs",
    "sessionID",
  ],
  computed: {
    noNameHeaderHeaders() {
      return this.headers.slice(1, this.headers.length);
    },
  },
  data: () => ({
    isAddingColumn: true,
    isEditingNewColumnName: false,
    editedHeader: {},
  }),
  methods: {
    something() {
      console.log("something clicked  ");
    },
    absHref(url) {
      return `https://${url}`;
    },
    formatPrice(value) {
      if (value) {
        return `$${value.toFixed(2)}`;
      } else {
        return value;
      }
    },
    toggleAddColumn() {
      this.isAddingColumn = !this.isAddingColumn;
    },
    addColumn(payload) {
      this.$emit("addColumn", payload);
    },
    getColumnUniqueId(i) {
      return this.alphabet[i].toUpperCase();
    },
    shouldShowRemoveColumn(index) {
      let isOriginalColumn = false;
      this.originalColumnsIDs.forEach((col, i) => {
        if (i === index) {
          isOriginalColumn = true;
        }
      });
      return !isOriginalColumn;
    },
    removeNewColumn(header) {
      this.$emit("removeNewColumn", header);
    },
    editNewColumnName(header, index) {
      // console.log("editing ", header);
      this.editedHeader = header;
      this.editedHeader.uniqueId = this.alphabet[index].toUpperCase();
      this.isEditingNewColumnName = true;
    },
    closeEditColumnNameDialog() {
      this.isEditingNewColumnName = false;
      this.editedHeader = {};
    },
    updateColumnName(header) {
      this.$emit("updateNewColumnName", header);
      this.closeEditColumnNameDialog();
    },
    headerMouseover(header) {
      header.isHover = true;
    },
    headerMouseleave(header) {
      header.isHover = false;
    },
    logLinkClickEvent(link) {
      let body = {
        event: "link-click",
        link: link,
        originateFrom: this.$options.name,
        sessionID: this.sessionID,
        timestamp: Date.now(),
      };
      console.log("body ", body);
      logEvent(body);
    },
  },
};
</script>
