<template>
  <v-container>
    <v-layout justify-center align-center>
      <v-col>
        <v-row align="center">
          <v-img src="logo_bb_offset_9.0.svg" max-width="64"></v-img>

          <h2>Engora Catalog Search </h2>

          <HowToDialog
            v-if="isFirstSearchDone"
            :isShown="isHowToDialogShown"
            @open="openHowToDialog"
            @close="closeHowToDialog"
          />
          <HowToText v-if="!isFirstSearchDone" />
        </v-row>

        <!--        {{ newColumns }}-->

        <v-row align="center">
          <v-text-field
            class="shrink"
            label="Product"
            v-model="searchTerm"
          ></v-text-field>
        </v-row>

        <v-row>
          <v-btn
            @click="addBlankRequirement"
            :disabled="isAddRequirementDisabled"
            elevation="1"
          >
            Add Requirement
          </v-btn>
        </v-row>
        <v-row>
          <v-list-item v-for="(req, index) in requirements" :key="req.id">
            <Requirement
              :index="index"
              :req="req"
              :isAdvanced="isAdvanced"
              @remove="removeRequirement($event)"
              @typeChange="setRequirementType($event)"
              @unitChange="setRequirementUnit($event)"
              @specNameInput="setRequirementSpecName($event)"
            />
          </v-list-item>
        </v-row>

        <v-row>
          <v-btn
            depressed
            color="primary"
            elevation="2"
            class="mt-4 mb-4"
            :disabled="!isSearchable"
            :loading="isGettingSearchResults"
            @click="manualSearchProducts"
          >
            Search
          </v-btn>
        </v-row>

        <v-row align="center">
          <div class="mr-3" style="user-select: none">Simple</div>
          <v-switch v-model="isAdvanced" class="mr-1"></v-switch>
          <div style="user-select: none">Advanced</div>
        </v-row>

        <v-row>
          <NoResults v-if="isNoResults" />
        </v-row>
        <v-row class="flex-column">
          <h2>Try one of these searches:</h2>
          <div class="mt-4 mb-4">
            <v-btn
              elevation="1"
              class="mr-4 mb-4"
              @click="setRequirementsPreset(presetsKeys.MOTORS)"
              >Motors by torque and speed</v-btn
            >
            <v-btn
              elevation="1"
              class="mr-4 mb-4"
              @click="setRequirementsPreset(presetsKeys.BRUSHLESS_MOTORS)"
              >Brushless motors by weight and thrust</v-btn
            >
            <v-btn
              elevation="1"
              class="mb-4"
              @click="setRequirementsPreset(presetsKeys.BATTERIES)"
              >Batteries by capacity</v-btn
            >
          </div>
        </v-row>

        <v-row v-if="resultsArrived">
          <Results
            :results="results"
            :parser="parser"
            :alphabet="alphabet"
            :sessionID="sessionID"
            :originalColumnsIDs="originalColumnsIDs"
            @updateResultsData="updateResultsWithNewColumn($event)"
            @removeNewColumn="removeNewColumn($event)"
            @updateColumnNameQueryParam="updateColumnNameQueryParam"
          />
        </v-row>
      </v-col>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";
import FormulaParser from "hot-formula-parser/parser";
import Vue from "vue";
import Requirement from "@/components/Requirement";
import HowToText from "@/components/HowToText";
import Results from "@/components/Results";
import HowToDialog from "@/components/HowToDialog";
import NoResults from "@/components/NoResults";
import { v4 as uuidv4 } from "uuid";
import * as Bowser from "bowser";
import { logEvent } from "@/services/logger";

export default {
  name: "Search",
  components: {
    Requirement,
    HowToText,
    Results,
    HowToDialog,
    NoResults,
  },
  computed: {
    isAddRequirementDisabled() {
      return !(this.requirements.length < this.maxRequirements);
    },
    resultsArrived() {
      return this.results.length > 0;
    },
    isSearchable() {
      return this.searchTerm.length > 0;
    },
  },
  data: () => ({
    searchTerm: "",
    requirements: [],
    queryString: [],
    maxRequirements: 5,
    presetSearchTerms: {
      MOTORS: "motor",
      BRUSHLESS_MOTORS: "motor",
      BATTERIES: "battery",
    },
    presetsKeys: {
      MOTORS: "MOTORS",
      BRUSHLESS_MOTORS: "BRUSHLESS_MOTORS",
      BATTERIES: "BATTERIES",
    },
    presets: {
      MOTORS: [
        {
          id: 1,
          type: "numeric",
          value: "speed",
          unit: "angular_speed",
        },
        {
          id: 2,
          type: "numeric",
          value: "torque",
          unit: "torque",
        },
      ],
      BRUSHLESS_MOTORS: [
        {
          id: 3,
          type: "numeric",
          value: "weight",
          unit: "mass",
        },
        {
          id: 4,
          type: "numeric",
          value: "thrust",
          unit: "force",
        },
        {
          id: 5,
          type: "category",
          value: "brushless",
        },
      ],
      BATTERIES: [
        {
          id: 6,
          type: "numeric",
          value: "capacity",
          unit: "current_capacity",
        },
        {
          id: 7,
          type: "numeric",
          value: "voltage",
          unit: "voltage",
        },
      ],
    },
    results: [],
    activePreset: null,
    isGettingSearchResults: false,
    parser: null,
    alphabet: [
      "a",
      "b",
      "c",
      "d",
      "e",
      "f",
      "g",
      "h",
      "i",
      "j",
      "k",
      "l",
      "m",
      "n",
      "o",
      "p",
      "q",
      "r",
      "s",
      "t",
      "u",
      "v",
      "w",
      "x",
      "y",
      "z",
    ], // currently supports 26 columns (A-Z) - can be extended by simply adding keys to this alphabet array
    originalColumnsIDs: [],
    newColumns: [],
    isFirstSearchDone: false,
    isHowToDialogShown: false,
    isNoResults: false,
    sessionID: null,
    isAdvanced: false,
    heartbeatInterval: null,
  }),
  methods: {
    addBlankRequirement() {
      if (this.requirements.length < this.maxRequirements) {
        const uniqueID = this.getUniqueID();
        this.requirements.push({
          id: uniqueID,
          type: "numeric",
          value: "",
          unit: "any",
        });
      }
    },
    removeRequirement(indexToRemove) {
      if (this.requirements[indexToRemove]) {
        this.requirements.splice(indexToRemove, 1);
      }
    },
    setRequirementsPreset(key) {
      if (this.presets[key]) {
        this.searchTerm = this.presetSearchTerms[key];
        this.requirements.splice(0, this.requirements.length);
        this.activePreset = this.presets[key];
        this.requirements.push(...this.presets[key]);
        this.logPresetClickEvent(key);
      }
    },
    setRequirementType(data) {
      this.requirements[data.index].type = data.type;
    },
    setRequirementUnit(data) {
      this.requirements[data.index].unit = data.unit;
    },
    setRequirementSpecName(data) {
      this.requirements[data.index].value = data.value;
    },
    setQueryString() {
      this.queryString = [];
      this.requirements.forEach((requirement) => {
        // Temp - until backend naming is changed use old format "unit" && "property"
        // mapping the UI text "numeric" "category" to DB values - unit, property
        let requirementType = requirement.type;
        if (requirementType === "numeric") {
          requirementType = "unit";
        }
        if (requirementType === "category") {
          requirementType = "property";
        }

        const INFER = "infer";
        this.queryString.push([
          this.isAdvanced ? requirementType : INFER,
          requirement.value,
          this.isAdvanced ? requirement.unit : INFER,
        ]);
      });
    },
    handleNoResultsSearch(responseLength) {
      // TBD - pop a modal stating there are no results and guiding the user on what they can search ?
      if (responseLength === 0) {
        this.isNoResults = true;
        this.logNoResultsEvent();
      } else {
        this.isNoResults = false;
      }
    },
    resetSearch() {
      this.results.splice(0, this.results.length);
    },
    manualSearchProducts() {
      this.searchProducts(true);
    },
    searchProducts(shouldResetColumnsQueryParams) {
      this.logSearchEvent();
      this.isGettingSearchResults = true;
      this.resetSearch();
      this.setQueryString();
      // TODO - before going live change to /search
      // const baseURL = "https://search.engora.tech";
      const baseURL2 = "http://vultadata.duckdns.org";
      axios
        .put(baseURL2 + "/search", {
          product: this.searchTerm,
          qs: this.queryString,
        })
        .then((response) => {
          // console.log("response data ", response.data);
          this.handleNoResultsSearch(response.data.length);
          this.isFirstSearchDone = true;
          if (response.data.length > 0) {
            this.results = response.data;
            this.setOriginalColumnIDs();
            if (!shouldResetColumnsQueryParams) {
              this.shouldInitCustomColumns();
            }

            this.handleSearchNavigation(
              this.searchTerm,
              shouldResetColumnsQueryParams
            );
          }
          this.isGettingSearchResults = false;
        });
    },
    setOriginalColumnIDs() {
      this.originalColumnsIDs = [];
      for (const [key] of Object.entries(this.results[0].values)) {
        this.originalColumnsIDs.push(key);
      }
    },
    shouldInitCustomColumns() {
      let columns = this.$route.query.columns;
      if (columns) {
        let tableFormattedCols = this.getDataTableFormattedColumns(columns);
        this.shouldSetNewColumns(tableFormattedCols);
        this.updateResultsWithNewColumns(tableFormattedCols);
      }
    },
    getDataTableFormattedColumns(cols) {
      // handle single query param columns as string (more than one array)
      if (typeof cols === "string") {
        cols = new Array(cols);
      }
      let formattedCols = [];
      cols.forEach((col) => {
        let noCurlyBrackets = col.replace(/{|}/gm, "");
        let keyValues = noCurlyBrackets.split(",");
        let keyValuesMatrix = keyValues.map((entry) => {
          return entry.split(":");
        });

        let JSONCol = this.getJSONCol(keyValuesMatrix);
        formattedCols.push(JSONCol);
      });
      return formattedCols;
    },
    getJSONCol(keyValuesMatrix) {
      let JSONCol = {
        formula: {},
        header: {},
      };
      keyValuesMatrix.forEach((entry) => {
        if (entry[0] === "k") {
          JSONCol.formula["key"] = entry[1];
          JSONCol.header["value"] = entry[1];
        } else if (entry[0] === "f") {
          JSONCol.formula["value"] = entry[1];
        } else if (entry[0] === "h") {
          JSONCol.header["text"] = entry[1];
        }
      });
      return JSONCol;
    },
    shouldInitPageByQueryParams() {
      let term = this.$route.query.term;
      let reqs = this.$route.query.reqs;
      if (term && reqs) {
        this.isAdvanced = JSON.parse(this.$route.query.isAdvanced);
        this.isFirstSearchDone = true;
        this.searchTerm = term;
        this.requirements = [];
        if (Array.isArray(reqs)) {
          reqs.forEach((req) => {
            let jsonReq = this.getJSONReq(req);
            this.requirements.push(jsonReq);
          });
        } else {
          let jsonReq = this.getJSONReq(reqs);
          this.requirements.push(jsonReq);
        }
        this.searchProducts(false);
      }
    },
    getJSONReq(req) {
      let noCurlyBrackets = req.replace(/{|}/gm, "");
      let keyValues = noCurlyBrackets.split(",");
      let keyValuesMatrix = keyValues.map((entry) => {
        return entry.split(":");
      });
      let JSONreq = {};
      keyValuesMatrix.forEach((entry) => {
        if (entry[0] === "id") {
          JSONreq[entry[0]] = entry[1];
        } else if (entry[0] === "t") {
          let UIpresantationType;
          // Temp - until backend naming is changed use old format "unit" && "property"
          // mapping the DB values - unit, property to UI text - "numeric" "category"
          if (entry[1] === "unit") {
            UIpresantationType = "numeric";
          } else if (entry[1] === "property") {
            UIpresantationType = "category";
          }
          JSONreq["type"] = UIpresantationType;
        } else if (entry[0] === "k") {
          JSONreq["value"] = entry[1];
        } else if (entry[0] === "u") {
          JSONreq["unit"] = entry[1];
        }
      });
      return JSONreq;
    },
    getStringReqsQueryParams(term, reqs) {
      // map requirements key values
      let stringReqs = [];
      reqs.forEach((req, i) => {
        let reqObject = `{id:${i + 1},t:${req[0]},k:${req[1]},u:`;
        if (req[2]) {
          reqObject += req[2];
        } else {
          reqObject += "none";
        }
        reqObject += "}";
        stringReqs.push(reqObject);
      });

      return stringReqs;
    },
    handleSearchNavigation(term, shouldResetColumnsQueryParams) {
      let reqs = this.getStringReqsQueryParams(
        this.searchTerm,
        this.queryString
      );
      let isNewSearch = true;

      let isAboutToNavToSameRoute = this.$route.name === "Search";
      if (isAboutToNavToSameRoute) {
        let routeReqs = this.$route.query.reqs;
        if (!Array.isArray(routeReqs)) {
          routeReqs = [routeReqs];
        }
        let isSameReqsLength = routeReqs.length === reqs.length;
        if (isSameReqsLength) {
          let equalReqsCounter = 0;
          reqs.forEach((req, index) => {
            if (req === routeReqs[index]) {
              equalReqsCounter++;
            }
          });
          if (equalReqsCounter === reqs.length) {
            isNewSearch = false;
          }
        }
      }

      if (isNewSearch || shouldResetColumnsQueryParams) {
        this.$router.push({
          path: "search",
          query: {
            term: term,
            reqs: reqs,
            columns: undefined,
            isAdvanced: this.isAdvanced,
          },
        });
      }
    },
    shouldSetNewColumns(queryParamColumns) {
      queryParamColumns.forEach((col) => {
        this.setNewColumns(col);
      });
    },
    setNewColumns(columnData) {
      let isNewKey = false;
      this.newColumns.forEach((col) => {
        if (col.formula.key === columnData.formula.key) {
          isNewKey = true;
        }
      });

      if (!isNewKey) {
        this.newColumns.push(columnData);
      }
    },
    updateSingleNewColumn(header) {
      this.newColumns.forEach((col) => {
        if (col.formula.value === header.value) {
          col.header.text = header.text;
          col.formula.key = header.text;
        }
      });
    },
    updateSingleColumnQueryParams() {
      let columnsStrings = this.getStringColumnsQueryParams(this.newColumns);

      let term = this.$route.query.term;
      let reqs = this.$route.query.reqs;

      this.$router.replace({
        path: "search",
        query: {
          term: term,
          reqs: reqs,
          columns: columnsStrings,
          isAdvanced: this.isAdvanced,
        },
      });
    },
    getStringColumnsQueryParams(cols) {
      let stringCols = [];
      cols.forEach((col) => {
        let colObject = `{k:${col.formula.key},f:${col.formula.value},h:${col.header.text}`;
        colObject += "}";
        stringCols.push(colObject);
      });
      return stringCols;
    },
    updateResultsWithNewColumn(columnPayload) {
      this.setNewColumns(columnPayload);
      this.updateSingleColumnQueryParams();
      let columns = [columnPayload];
      this.updateResultsWithNewColumns(columns);
    },
    removeColumnsQueryParamByKey(key) {
      let tableFormattedCols = this.getDataTableFormattedColumns(
        this.$route.query.columns
      );
      // update new columns, remove selected key query param
      tableFormattedCols.forEach((col, index) => {
        this.setNewColumns(col);
        if (col.formula.key === key) {
          this.newColumns.splice(index, 1);
        }
      });

      // update columns query param without deleted
      this.updateSingleColumnQueryParams();
    },
    removeNewColumn(payload) {
      this.results.forEach((entry) => {
        if (entry.values[payload.value]) {
          Vue.delete(entry.values, payload.value);
          // delete entry.values[payload.value];
        }
      });

      // console.log("results[0].values ", this.results[0].values);

      this.removeColumnsQueryParamByKey(payload.value);
    },
    updateResultsWithNewColumns(columns) {
      this.results.forEach((entry) => {
        columns.forEach((columnPayload) => {
          this.shouldAddNewColumnCellData(columnPayload, entry);
        });
      });
      // console.log("this updated results ", this.results);
    },
    shouldAddNewColumnCellData(payload, entry) {
      // currently allow for resetting existing values
      // if (!(payload.formula.key in entry.values)) {
      // ...
      // }

      // verify not trying to change original columns
      let isTryChangeOriginalColumns = this.isTryChangeOriginalColumn(
        payload.formula.key
      );
      if (!isTryChangeOriginalColumns) {
        let noVariablesFormula = this.getNoVariablesFormula(payload, entry);
        const output = this.parse(noVariablesFormula);
        entry.values[payload.formula.key] = [output.result, output.result];
      }
    },
    isTryChangeOriginalColumn(key) {
      let isTryChangeOriginalColumns = false;
      this.originalColumnsIDs.forEach((original) => {
        if (original === key) {
          isTryChangeOriginalColumns = true;
        }
      });
      return isTryChangeOriginalColumns;
    },
    getNoVariablesFormula(payload, entry) {
      let indexCounter = 0;
      let noVariablesFormula = payload.formula.value;
      // parse formula with concrete row data
      for (const [, value] of Object.entries(entry.values)) {
        let columnID = this.alphabet[indexCounter].toUpperCase();
        let match = noVariablesFormula.match(columnID);
        if (match) {
          noVariablesFormula = noVariablesFormula.replace(columnID, value[0]);
        }
        indexCounter++;
      }
      return noVariablesFormula;
    },
    initParser() {
      this.parser = new FormulaParser();
    },
    parse(string) {
      return this.parser.parse(string);
    },
    updateColumnNameQueryParam(header) {
      this.updateSingleNewColumn(header);
      this.updateSingleColumnQueryParams();
    },
    closeHowToDialog() {
      this.isHowToDialogShown = false;
    },
    openHowToDialog() {
      this.isHowToDialogShown = true;
    },
    setSessionID() {
      this.sessionID = uuidv4();
    },
    // * * * Log Triggers * * *
    logPageLoadEvent() {
      let body = {
        event: "page-load",
        sessionID: this.sessionID,
        timestamp: Date.now(),
        queryParams: this.$route.query,
        screenSize: {
          height: window.screen.height,
          width: window.screen.width,
        },
        browser: this.getBrowser(),
      };
      logEvent(body);
    },
    logSearchEvent() {
      let body = {
        event: "search",
        sessionID: this.sessionID,
        timestamp: Date.now(),
        term: this.searchTerm,
      };
      logEvent(body);
    },
    logPresetClickEvent(presetKey) {
      let body = {
        event: "preset",
        presetName: presetKey.toLowerCase(),
        sessionID: this.sessionID,
        timestamp: Date.now(),
      };
      logEvent(body);
    },
    initHeartbeatEvent() {
      let body = {
        event: "heartbeat",
        sessionID: this.sessionID,
        timestamp: Date.now(),
      };
      const duration = 2000;
      this.heartbeatInterval = setInterval(() => {
        logEvent(body);
      }, duration);
    },
    resetHeartbeatEvent() {
      clearInterval(this.heartbeatInterval);
      this.heartbeatInterval = null;
    },
    logNoResultsEvent() {
      let body = {
        event: "no-results",
        sessionID: this.sessionID,
        timestamp: Date.now(),
      };
      logEvent(body);
    },
    // * * * Util * * *
    getUniqueID() {
      return Math.floor(Math.random() * 100000);
    },
    getBrowser() {
      return Bowser.getParser(window.navigator.userAgent).getBrowserName();
    },
  },
  beforeMount() {
    this.setSessionID();
    this.initHeartbeatEvent();
    this.logPageLoadEvent();
    this.initParser();
    this.shouldInitPageByQueryParams();
  },
  beforeDestroy() {
    this.resetHeartbeatEvent();
  },
};
</script>
